const API_KEY = process.env.NEXT_PUBLIC_GOOGLE_API_KEY!;

// SEND OTP
export async function sendOtp(phone: string) {
  const res = await fetch(
    `https://identitytoolkit.googleapis.com/v1/accounts:sendVerificationCode?key=${API_KEY}`,
    {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        phoneNumber: phone,
        recaptchaToken: "test", // works ONLY for test numbers
      }),
    }
  );

  const data = await res.json();
  if (data.error) throw new Error(data.error.message);
  return data.sessionInfo;
}

// VERIFY OTP
export async function verifyOtp(sessionInfo: string, otp: string) {
  const res = await fetch(
    `https://identitytoolkit.googleapis.com/v1/accounts:signInWithPhoneNumber?key=${API_KEY}`,
    {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        sessionInfo,
        code: otp,
      }),
    }
  );

  const data = await res.json();
  if (data.error) throw new Error(data.error.message);
  return data;
}
